import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actionCreators from '../../store/actions/index';

import CounterControl from '../../components/CounterControl/CounterControl';
import CounterOutput from '../../components/CounterOutput/CounterOutput';

class Counter extends Component {
    render () {
        const button = {boxSizing:'border-box',margin:'10px',padding: '10px',background:'white',border: '1px solid orange', borderRadius:'10px',color :'orange'};
       // style={{background:'linearGradient( #202020, #cfbaa8)'}}
        return (
            <div style ={{height:'1920px',backgroundColor:'rgb(25,25,25)',boxSizing:'border-box'}}>
                <CounterOutput value={this.props.ctr} />
                <CounterControl label="+ 1" clicked={this.props.onIncrementCounter} />
                <CounterControl label="- 1" clicked={this.props.onDecrementCounter}  />
                <CounterControl label="+ 10" clicked={this.props.onAddCounter}  />
                <CounterControl label="- 15" clicked={this.props.onSubtractCounter}  />
                <hr />
                <button style={button} onClick={() => this.props.onStoreResult(this.props.ctr)}><strong>STORE RESULT</strong></button>
                <ul style ={{boxSizing :'border-box'}}>
                    {this.props.storedResults.map(strResult => (
                       <li  style={{color:'white'}} key={strResult.id}>  {strResult.value} &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;    &nbsp;     <button style={{border: '1px solid red',align:'right', background:'white',borderRadius:'10px',color :'red'}}onClick={() => this.props.onDeleteResult(strResult.id)}> <strong>-</strong></button> <p></p></li>
                    ))}
                </ul>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {

        ctr: state.ctr.counter,
        storedResults: state.res.results
    }
};

const mapDispatchToProps = dispatch => {
    return {
        onIncrementCounter: () => dispatch(actionCreators.increment()),
        onDecrementCounter: () => dispatch(actionCreators.decrement()),
        onAddCounter: () => dispatch(actionCreators.add(10)),
        onSubtractCounter: () => dispatch(actionCreators.subtract(15)),
        onStoreResult: (result) => dispatch(actionCreators.storeResult(result)),
        onDeleteResult: (id) => dispatch(actionCreators.deleteResult(id))
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(Counter);


